"use client";

import { useState } from "react";
import styles from "./RequestForm.module.css";

export default function RequestFormPage() {
  const [status, setStatus] = useState("");
  const [submitting, setSubmitting] = useState(false);
  const [step, setStep] = useState(0);
  const [form, setForm] = useState({
    name: "",
    email: "",
    company: "",
    phone: "",
    product: "",
    quantity: "",
    message: ""
  });

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setStatus("");
    setSubmitting(true);

    if (step < steps.length - 1) {
      handleNext();
      setSubmitting(false);
      return;
    }

    if (!form.name || !form.email) {
      setStatus("Name and work email are required.");
      setSubmitting(false);
      return;
    }

    try {
      const response = await fetch("/api/requests", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form)
      });

      if (!response.ok) {
        throw new Error("Unable to submit request.");
      }

      setStatus("Thanks! We'll reach out within 24 hours with a quote.");
      setForm({
        name: "",
        email: "",
        company: "",
        phone: "",
        product: "",
        quantity: "",
        message: ""
      });
    } catch (error) {
      setStatus("Something went wrong. Please try again.");
    } finally {
      setSubmitting(false);
    }
  };

  const handleNext = () => {
    if (step === 0 && (!form.name || !form.email)) {
      setStatus("Please add your name and work email.");
      return;
    }
    setStatus("");
    setStep((prev) => Math.min(prev + 1, 2));
  };

  const handleBack = () => {
    setStatus("");
    setStep((prev) => Math.max(prev - 1, 0));
  };

  const steps = [
    { title: "Contact", description: "Tell us who to follow up with." },
    { title: "Company", description: "Share your company and request basics." },
    { title: "Details", description: "Add any notes or timelines." }
  ];

  return (
    <section className={styles.page}>
      <div className={`container ${styles.layout}`}>
        <div className={styles.copy}>
          <h1>Request a quote.</h1>
          <div className={styles.trustRow}>
            <span>Proof before print</span>
            <span>Quotes in 24 hours</span>
            <span>Reorder-ready</span>
          </div>
          <p>
            Share your product needs and timelines. Our team responds within 24 hours
            with a supplier-ready quote.
          </p>
        </div>
        <form className={styles.form} onSubmit={handleSubmit}>
          <div className={styles.stepHeader}>
            <div>
              <p className={styles.stepEyebrow}>
                Step {step + 1} of {steps.length}
              </p>
              <h2>{steps[step].title}</h2>
              <p className={styles.stepDescription}>{steps[step].description}</p>
            </div>
            <div className={styles.stepper} aria-hidden="true">
              {steps.map((item, index) => (
                <span
                  key={item.title}
                  className={`${styles.stepDot} ${index <= step ? styles.stepDotActive : ""}`}
                ></span>
              ))}
            </div>
          </div>

          {step === 0 ? (
            <div className={styles.formRow}>
              <label>
                Name *
                <input
                  type="text"
                  required
                  value={form.name}
                  onChange={(event) => handleChange("name", event.target.value)}
                />
              </label>
              <label>
                Work email *
                <input
                  type="email"
                  required
                  value={form.email}
                  onChange={(event) => handleChange("email", event.target.value)}
                />
              </label>
            </div>
          ) : null}

          {step === 1 ? (
            <>
              <div className={styles.formRow}>
                <label>
                  Company
                  <input
                    type="text"
                    value={form.company}
                    onChange={(event) => handleChange("company", event.target.value)}
                  />
                </label>
                <label>
                  Phone
                  <input
                    type="tel"
                    value={form.phone}
                    onChange={(event) => handleChange("phone", event.target.value)}
                  />
                </label>
              </div>
              <div className={styles.formRow}>
                <label>
                  Product interest
                  <input
                    type="text"
                    value={form.product}
                    onChange={(event) => handleChange("product", event.target.value)}
                  />
                </label>
                <label>
                  Estimated quantity
                  <input
                    type="text"
                    value={form.quantity}
                    onChange={(event) => handleChange("quantity", event.target.value)}
                  />
                </label>
              </div>
            </>
          ) : null}

          {step === 2 ? (
            <label className={styles.fullWidth}>
              Project details
              <textarea
                rows={6}
                value={form.message}
                onChange={(event) => handleChange("message", event.target.value)}
              ></textarea>
            </label>
          ) : null}

          <div className={styles.formActions}>
            <button
              className="btn btn-secondary"
              type="button"
              onClick={handleBack}
              disabled={step === 0}
            >
              Back
            </button>
            {step < steps.length - 1 ? (
              <button className="btn btn-primary" type="button" onClick={handleNext}>
                Continue
              </button>
            ) : (
              <button className="btn btn-primary" type="submit" disabled={submitting}>
                {submitting ? "Sending..." : "Request a Quote"}
              </button>
            )}
          </div>

          <p className={styles.formNote} role="status" aria-live="polite">
            {status}
          </p>
        </form>
      </div>
    </section>
  );
}
